<script>
import {Table, TableColumn} from 'element-ui'
import axios from "axios";
import {baseUrl} from "../../../config/index.json";

export default {
  name: 'light-table',
  components: {
    [Table.name]: Table,
    [TableColumn.name]: TableColumn
  },
  data() {
    return {
      selected: [], // Array reference
      options: [
      ],
      is_negative:false,
      is_positive:false,
      selected1: [], // Array reference
      options1: [
      ],
      imageUrl:[],
      myImages:[],
      files_name:[],
      profession_aspects:'',
      profession_skills:'',
      profession_name:'',
      profession_type:'',
      min_salary:'',
      mid_salary:'',
      max_salary:'',
      workplace:'',
      working_condition:'',
      subject:'',
      practice:'',
      higher_education_info:'',
      about:'',
      title:'',
      //ru
      profession_aspectsru:'',
      profession_skillsru:'',
      profession_nameru:'',
      profession_typeru:'',
      min_salaryru:'',
      mid_salaryru:'',
      max_salaryru:'',
      workplaceru:'',
      working_conditionru:'',
      subjectru:'',
      practiceru:'',
      higher_education_inforu:'',
      aboutru:'',
      titleru:'',
      order:'',
      //en
      profession_aspectsen:'',
      profession_skillsen:'',
      profession_nameen:'',
      profession_typeen:'',
      min_salaryen:'',
      mid_salaryen:'',
      max_salaryen:'',
      workplaceen:'',
      working_conditionen:'',
      subjecten:'',
      practiceen:'',
      higher_education_infoen:'',
      abouten:'',
      titleen:''
    };
  },
  methods: {
    trySubmitFile(e) {
      for (let i = 0; i < e.target.files.length; i++) {
        this.files_name.push(e.target.files[i])
      }
      var files = Array.from(e.target.files);
      console.log(typeof (files))
      files.forEach((file) => {
        this.myImages.push(file);
        var reader = new FileReader();
        var vm = this;
        reader.onload = function(e) {
          vm.imageUrl.push(e.target.result);
        };
        reader.readAsDataURL(file);
      });
    },
    rigt_side() {
      this.right_t = !this.right_t
      this.isButton=false
    },
    updatePage(item) {
      this.data_main = Users[item - 1]
    },
    createOz(){
      const form = new FormData();
      form.append("banner_image", this.myImages[0]);
      form.append("cover_image", this.myImages[1]);
      form.append("title", this.title);
      form.append("about", this.about);
      form.append("higher_education_info", this.higher_education_info);
      form.append("practice", this.practice);
      form.append("subject", this.subject);
      form.append("working_condition", this.working_condition);
      form.append("workplace", this.workplace);
      form.append("max_salary", this.max_salary);
      form.append("mid_salary", this.mid_salary);
      form.append("min_salary", this.min_salary);
      form.append("profession_type", this.selected);
      form.append("profession_name", this.profession_name);
      form.append("subjects", this.selected1);
      axios.post(`${baseUrl}professions/`,form,
        {
          headers: {
            'Authorization': `Bearer ${localStorage.getItem('token')}`,
            'Accept-Language':'oz'
          }
        })
        .then(res=>{
          this.$router.push(`/profession/${res.data.id}/skills`)
          this.title=''
          this.description=''
          this.order=''
          this.myImages=[]
          this.$toast("Ma'lumotlar o'zbek tilida kiritildi", {
            timeout: 2000,
            type:"success"
          });
          //ru
          form.append("title", this.titleru);
          form.append("about", this.aboutru);
          form.append("higher_education_info", this.higher_education_inforu);
          form.append("practice", this.practiceru);
          form.append("subject", this.subjectru);
          form.append("working_condition", this.working_conditionru);
          form.append("workplace", this.workplaceru);
          axios.patch(`${baseUrl}professions/${res.data.id}/`,form,
            {
              headers: {
                'Authorization': `Bearer ${localStorage.getItem('token')}`,
                'Accept-Language':'ru'
              }
            })
            .then(ress=>{

              axios.post(`${baseUrl}skills/`,{
                  "content":this.profession_skills,
                  "profession":ress.data.id
                },
                {
                  headers: {
                    'Authorization': `Bearer ${localStorage.getItem('token')}`,
                    'Accept-Language':'ru'
                  }
                })

              axios.post(`${baseUrl}aspects/`,{
                  "content":this.profession_skills,
                  "profession":ress.data.id,
                  "is_negative":this.is_negative,
                  "is_positive":this.is_positive
                },
                {
                  headers: {
                    'Authorization': `Bearer ${localStorage.getItem('token')}`,
                    'Accept-Language':'oz'
                  }
                })
              this.titleru=''
              this.descriptionru=''
              this.order=''
              this.myImages=[]
              this.$toast("Ma'lumotlar rus tilida kiritildi", {
                timeout: 2000,
                type:"success"
              });
              //en

              form.append("title", this.titleen);
              form.append("about", this.abouten);
              form.append("higher_education_info", this.higher_education_infoen);
              form.append("practice", this.practiceen);
              form.append("subject", this.subjecten);
              form.append("working_condition", this.working_conditionen);
              form.append("workplace", this.workplaceen);
              axios.patch(`${baseUrl}professions/${res.data.id}/`,form,
                {
                  headers: {
                    'Authorization': `Bearer ${localStorage.getItem('token')}`,
                    'Accept-Language':'kar'
                  }
                })
                .then(resss=>{

                  axios.post(`${baseUrl}skills/`,{
                      "content":this.profession_skills,
                      "profession":resss.data.id
                    },
                    {
                      headers: {
                        'Authorization': `Bearer ${localStorage.getItem('token')}`,
                        'Accept-Language':'kar'
                      }
                    })

                  axios.post(`${baseUrl}aspects/`,{
                      "content":this.profession_skills,
                      "profession":resss.data.id,
                      "is_negative":this.is_negative,
                      "is_positive":this.is_positive
                    },
                    {
                      headers: {
                        'Authorization': `Bearer ${localStorage.getItem('token')}`,
                        'Accept-Language':'kar'
                      }
                    })
                  this.titleen=''
                  this.descriptionen=''
                  this.order=''
                  this.myImages=[]
                  this.$toast("Ma'lumotlar barcha tillarda kiritildi", {
                    timeout: 2000,
                    type:"success"
                  });
                })
            })
        })
    },

  },
  mounted() {
    axios.get(`${baseUrl}subjects/`)
      .then(res=>{
        res.data.forEach(el=>{
          console.log(el)
          this.options.push({
